



























































































































































import Vue from 'vue'
import anime from 'animejs'

export default Vue.extend({
  name: 'WelcomeIllustration',

  mounted() {
    anime({
      targets: '#illustration #Group path',
      duration: 1000,
      translateY: anime.random(-4, 4),
      loop: true,
      direction: 'alternate',
      easing: 'easeInOutQuad',
      delay: anime.stagger(100),
    })
  },
})
